<template>
  <div>
    <!-- header begin -->
    <!-- <div class="nav-bar">
      <div class="nav-bar-left" @click="goShglIndex()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">选择商品</div>
      <div class="nav-bar-right" @click="showPopup()">
        <i class="iconfont iconsousuo" />
      </div>
    </div> -->
    <!-- header end -->
    <headerBar :header-data="headerData" @searchClick="searchAction()" />
    <div class="container">
      <common-list
        ref="dida_list"
        class="common-list common-list-spe"
        :post-url="list.postUrl"
        :req-data="list.reqData"
        :data-name="list.dataName"
        :apptype-show="false"
        @returnData="getPrizeLog($event)"
      >

        <div class="common-main box">
          <!-- 循环列表 begin -->
          <div v-for="(items,indexs) of spxxList" :key="indexs" class="general-list">
            <div class="general-check"> <van-checkbox v-model="items.state" @click="checkMx(items,indexs)" /> </div>
            <div class="general-item" @click="showSpxzPopup(indexs)">
              <div class="general-tit">{{ items.htmc }}</div>
              <div class="general-info"><span><em>订单编号：</em>{{ items.htbh }}</span></div>
              <div class="general-info"><span><em>乙方名称：</em>{{ items.gysmc }}</span></div>
              <div class="general-info"><span><em>订单金额：</em>{{ items.htje+'(元)' }}</span></div>
            </div>
            <div class="general-arrow"><i class="iconfont iconarrowright" /></div>
          </div>
        <!-- 循环列表 end -->
        </div>
      </common-list>
    </div>
    <div class="common-space" />
    <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="save()">保存</van-button></li>
      </ul>
    </div>
    <!-- 删除操作按钮 -->
    <!-- <div :class="showDelete == true ? 'foot-btn fixed show':'foot-btn fixed hide'" style="z-index:91;">
      <ul>
        <li class="check" @click="checkAll">
          <van-checkbox v-model="checked" />
          全选
        </li>
        <li><van-button type="info" round size="large">删除（25）</van-button></li>
      </ul>
    </div> -->

    <!-- 选择商品弹出层 beign -->
    <van-popup
      v-model="SpxzShow"
      :get-container="getContainer"
      closeable
      position="bottom"
      close-icon-position="top-left"
      :style="{ height: '90%',background:'#f9f9f9' }"
      @click="showSearch = false"
      @close="close"
    >
      <div class="popup-title">选择商品</div>
      <div v-if="spxxList && spxxList[mxIndex]" class="popup-content">
        <!-- 商品列表 begin -->
        <div v-for="(item,index) of spxxList[mxIndex].mxList" :key="index" class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title-check">
            <div class="check"><van-checkbox v-model="item.state" @click="checkSp(item,index)" /></div>
            <div class="check-info">{{ item.spmc }}</div>
          </div>
          <!-- 通用标题 end -->
          <div class="page-list">
            <div class="pagelist-left">订单数量</div>
            <div class="pagelist-right">{{ item.sl }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">待交货数量</div>
            <div class="pagelist-right">{{ item.djhsl }}</div>
          </div>
          <!-- <van-field
            v-model="item.bcsl"
            v-validate="'required'"
            label="本次送货数量"
            input-align="right"
            label-width="100"
            type="number"
            name="bcsl"
            :error-message="errors.first('bcsl')"
            placeholder="请输入数量"
          /> -->
          <van-field
            v-model="item.bcsl"
            label="本次送货数量"
            input-align="right"
            label-width="100"
            type="number"
            placeholder="请输入数量"
          />
          <div class="page-list">
            <div class="pagelist-left">单价（含税）元</div>
            <div class="pagelist-right">{{ item.dj }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">计量单位</div>
            <div class="pagelist-right">{{ item.jldw }}</div>
          </div>
        </div>
        <!-- 商品列表 end -->
      </div>

      <div class="common-space" />
      <!-- 底部按钮 begin -->
      <div class="foot-btn fixed">
        <ul>
          <li class="check">
            <van-checkbox v-model="checked" @click="checkAll" />
            全选
          </li>
          <li><van-button type="info" round size="large" @click="checkFinish()">完成（{{ checkedMxList.length }}）</van-button></li>
        </ul>
      </div>
      <!-- 底部按钮 end -->

    </van-popup>
    <!-- 选择商品弹出层 end -->

    <searchMeun @goSearchAction="search($event)" @reSet="goReset">
      <van-field v-model="ddmc" label="订单名称" placeholder="请输入订单名称" input-align="right" />
      <van-field v-model="ddbh" label="订单编号" placeholder="请输入订单编号" input-align="right" />
    </searchMeun>
    <!-- 查询条件  begin -->
    <!-- <van-popup v-model="show" position="top" class="search-popup" overlay-class="transparent-overlay">
      <div class="common-main box">
        <div class="spe-input">
          <van-field v-model="ddmc" label="订单名称" placeholder="请输入订单名称" input-align="right" />
          <van-field v-model="ddbh" label="订单编号" placeholder="请输入订单编号" input-align="right" />
        </div>
        <div class="search-popup-btn">
          <van-button type="info" round plain block @click="goReset()">重置</van-button>
          <van-button type="info" block round @click="search()">搜索</van-button>
        </div>
      </div>
    </van-popup> -->
    <!-- 查询条件  end -->
  </div>
</template>

<script land="script" scoped>
import commonList from '@/components/list/commonList.vue'
import searchMeun from '@/components/searchComponents/searchMeun.vue'
import headerBar from '@/components/header/headerBar.vue'

export default {
  components: {
    commonList,
    searchMeun,
    headerBar
  },
  data() {
    return {
      headerData: {
        title: '选择商品',
        leftUrl: 'back',
        leftText: ' ',
        showSearch: true
      },
      show: false,
      pageno: 1,
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      ddmc: '',
      ddbh: '',
      id: '',
      fhmxList: [],
      checkedMxList: [], // 选中的商品明细信息
      mxIndex: 0,
      mxList: [],
      spxxList: [],
      gysList: [],
      fhbh: '',
      canChange: true,
      pageName: '', // 当前tab页页面
      fileChangeInfo: {},
      filegroup: '',
      fhsj: '',
      yjdhsj: '',
      YjdhsjShow: false,
      date1: '',
      date: '',
      wllxShow: '',
      wllx: '',
      bcshsl: '',
      checked: '',
      fhdbh: '',
      active: '',
      fhdmc: '',
      wlgsmc: '',
      ydh: '',
      fhdd: '',
      wllxr: '',
      wllxfs: '',
      xmmcvalue: '',
      xmbhvalue: '',
      FhsjShow: false,
      showDelete: false,
      SpxzShow: false,
      showSearch: false,
      result: [],
      result2: [],
      fileList: [
        { url: 'https://img.yzcdn.cn/vant/leaf.jpg' }
      ],
      searchPick: {
        PickTitle: '请选择物流类型',
        PickColumns: []
      },
      activeNames: ['1']
    }
  },
  computed: {

  },
  created() {
    this.fhmxList = this.$route.params.fhmxList
    this.init()
  },

  methods: {
    goReset() {
      this.ddmc = ''
      this.ddbh = ''
    },
    getPrizeLog(res) {
      this.spxxList = []
      for (let i = 0; i < res.length; i++) {
        const arr = {}
        arr.state = false
        for (let j = 0; j < res[i].length; j++) {
          const item = res[i][j]
          const key = item.name
          const value = item.value
          arr[key] = value
          if (item.mxList) {
            const array = []
            for (let k = 0; k < item.mxList.length; k++) {
              const element = item.mxList[k]
              const a = {}
              a.state = false
              for (let x = 0; x < element.length; x++) {
                const temp = element[x]
                const key = temp.name
                const value = temp.value
                a[key] = value
              }
              array.push(a)
            }
            arr['mxList'] = array
          }
        }
        this.spxxList.push(arr)
      }
    },
    search() {
      this.$bus.$emit('showMeun', false)
      const msg = {
        ddmc: this.ddmc,
        ddbh: this.ddbh
      }
      this.$refs.dida_list.onRefresh(msg)
      this.show = false
    },
    init() {
      this.list.postUrl = 'fhgl/getXm_fhdhmxSelectAction'
      this.list.reqData = {
        zzid: this.$store.state.mh.mhUserInfo.zzid,
        //  zzzid: '6e482fdbb581420bbd537cf8fb535954',
        pageno: this.pageno
      }
      this.list.dataName = 'list'
    },
    save() {
      for (let i = this.spxxList.length - 1; i >= 0; i--) {
        if (this.spxxList[i].state === false) {
          this.spxxList.splice(i, 1)
        } else {
          for (let j = this.spxxList[i].mxList.length - 1; j >= 0; j--) {
            if (this.spxxList[i].mxList[j].state === false) {
              this.spxxList[i].mxList.splice(j, 1)
            }
          }
        }
      }
      // if (this.fhmxList) {
      //   for (let i = 0; i < this.fhmxList.length; i++) {
      //     this.spxxList.push(this.fhmxList[i])
      //   }
      // }
      this.saveXm_fhmxList()
    },
    saveXm_fhmxList() {
      const arr = []
      for (let i = 0; i < this.spxxList.length; i++) {
        for (let j = 0; j < this.spxxList[i].mxList.length; j++) {
          const item = {}
          item.id = this.spxxList[i].mxList[j].id
          item.spmc = this.spxxList[i].mxList[j].spmc
          item.htid = this.spxxList[i].mxList[j].htid
          item.bcsl = this.spxxList[i].mxList[j].bcsl
          item.sqdmxid = this.spxxList[i].mxList[j].sqdmxid
          arr.push(item)
        }
      }
      this.$store.dispatch('fhgl/saveXm_fhmxListAction', {
        fhbh: this.$store.state.fhgl.fhbh,
        xm_fhmxList: arr
      }).then((res) => {
        this.$toast('保存成功！')
        // this.$store.commit('fhgl/SET_SPMXLIST', this.spxxList)
        this.$store.commit('fhgl/SET_ACTIVE', 1)
        this.$router.back()
      })
    },
    checkMx(items, indexs) {
      for (let i = 0; i < this.spxxList.length; i++) {
        for (let j = 0; j < this.spxxList[i].mxList.length; j++) {
          const item = this.spxxList[i].mxList[j]
          if (item.bcsl < 0) {
            this.$toast('本次送货数量应大于0！')
            this.spxxList[indexs].state = !this.spxxList[indexs].state
            this.spxxList.splice(indexs, 1, this.spxxList[indexs])
            return
          }
          if (item.bcsl > Number.parseInt(item.djhsl)) {
            this.$toast('本次送货数量不能大于待交货数量！')
            this.spxxList[indexs].state = !this.spxxList[indexs].state
            this.spxxList.splice(indexs, 1, this.spxxList[indexs])
            return
          }
        }
      }
      this.mxIndex = indexs
      if (items.state) {
        for (let i = 0; i < this.spxxList[indexs].mxList.length; i++) {
          if (this.spxxList[indexs].mxList[i].bcsl && this.spxxList[indexs].mxList[i].bcsl > 0) {
            this.spxxList[indexs].mxList[i].state = true
          } else {
            this.spxxList[indexs].mxList[i].bcsl = this.spxxList[indexs].mxList[i].djhsl
            this.spxxList[indexs].mxList[i].state = true
          }
        }
      } else {
        for (let i = 0; i < this.spxxList[indexs].mxList.length; i++) {
          this.spxxList[indexs].mxList[i].state = false
        }
      }
    },
    close() {
      if (this.checkedMxList.length > 0) {
        this.spxxList[this.mxIndex].state = true
        this.spxxList.splice(this.mxIndex, 1, this.spxxList[this.mxIndex])
      } else {
        this.spxxList[this.mxIndex].state = false
        this.spxxList.splice(this.mxIndex, 1, this.spxxList[this.mxIndex])
      }
    },
    showSpxzPopup(index) {
      this.mxIndex = index
      this.checkedMxList = []
      for (let i = 0; i < this.spxxList[index].mxList.length; i++) {
        const item = this.spxxList[index].mxList[i]
        if (item.state) {
          this.checkedMxList.push(this.spxxList[index].mxList[i])
        }
      }
      if (this.checkedMxList.length === this.spxxList[index].mxList.length) {
        this.checked = true
      } else {
        this.checked = false
      }
      this.SpxzShow = true
    },
    showPopup() {
      this.show = true
    },
    checkFinish() {
      for (let j = 0; j < this.spxxList[this.mxIndex].mxList.length; j++) {
        const item = this.spxxList[this.mxIndex].mxList[j]
        if (item.state && item.bcsl <= 0) {
          this.$toast('本次送货数量应大于0！')
          return
        }
        if (item.state && item.bcsl > Number.parseInt(item.djhsl)) {
          this.$toast('本次送货数量不能大于待交货数量！')
          return
        }
      }
      if (this.checkedMxList.length > 0) {
        this.spxxList[this.mxIndex].state = true
        this.spxxList.splice(this.mxIndex, 1, this.spxxList[this.mxIndex])
      } else {
        this.spxxList[this.mxIndex].state = false
        this.spxxList.splice(this.mxIndex, 1, this.spxxList[this.mxIndex])
      }
      this.SpxzShow = false
    },
    checkSp(item, index) {
      if (item.bcsl === '' || item.bcsl <= 0) {
        this.spxxList[this.mxIndex].mxList[index].bcsl = this.spxxList[this.mxIndex].mxList[index].djhsl
        this.spxxList[this.mxIndex].mxList.splice(index, 1, this.spxxList[this.mxIndex].mxList[index])
      }
      if (item.bcsl > Number.parseInt(item.djhsl)) {
        this.$toast('本次送货数量不能大于待交货数量！')
        this.spxxList[this.mxIndex].mxList[index].state = !this.spxxList[this.mxIndex].mxList[index].state
        this.spxxList[this.mxIndex].mxList.splice(index, 1, this.spxxList[this.mxIndex].mxList[index])
        return
      }
      if (item.state) {
        this.checkedMxList.push(item)
      } else {
        this.checkedMxList.splice(this.checkedMxList.indexOf(item), 1)
      }
      if (this.checkedMxList.length === this.spxxList[this.mxIndex].mxList.length) {
        this.checked = true
      } else {
        this.checked = false
      }
    },
    getContainer() {

    },
    goShglIndex() {
      this.$store.commit('fhgl/SET_ACTIVE', 1)
      this.$router.back()
    },
    checkAll() {
      let num1 = 0
      let num2 = 0
      for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
        if (this.spxxList[this.mxIndex].mxList[i].state) {
          num1++
        } else {
          num2++
        }
      }
      if (num1 === 0) {
        let temp = false
        for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
          if (!this.spxxList[this.mxIndex].mxList[i].bcsl || this.spxxList[this.mxIndex].mxList[i].bcsl <= 0) {
            temp = true
          }
        }
        if (temp) {
          for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
            this.spxxList[this.mxIndex].mxList[i].bcsl = this.spxxList[this.mxIndex].mxList[i].djhsl
          }
          for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
            this.spxxList[this.mxIndex].mxList[i].state = true
            this.spxxList[this.mxIndex].mxList.splice(i, 1, this.spxxList[this.mxIndex].mxList[i])
            this.spxxList.splice(this.mxIndex, 1, this.spxxList[this.mxIndex])
            this.checkedMxList.push(this.spxxList[this.mxIndex].mxList[i])
          }
          return
        } else {
          for (let i = 0; i < this.spxxList.length; i++) {
            for (let j = 0; j < this.spxxList[i].mxList.length; j++) {
              const item = this.spxxList[i].mxList[j]
              if (item.bcsl < 0) {
                this.$toast('本次送货数量应大于0！')
                this.checked = !this.checked
                return
              }
              if (item.bcsl > Number.parseInt(item.djhsl)) {
                this.$toast('本次送货数量不能大于待交货数量！')
                this.checked = !this.checked
                return
              }
            }
          }
        }
        for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
          this.spxxList[this.mxIndex].mxList[i].state = true
          this.spxxList[this.mxIndex].mxList.splice(i, 1, this.spxxList[this.mxIndex].mxList[i])
          this.spxxList.splice(this.mxIndex, 1, this.spxxList[this.mxIndex])
          this.checkedMxList.push(this.spxxList[this.mxIndex].mxList[i])
        }
      }
      if (num2 === 0) {
        let temp = false
        for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
          if (!this.spxxList[this.mxIndex].mxList[i].bcsl) {
            temp = true
          }
        }
        if (temp) {
          this.checked = false
          this.$toast('请先选择本次发货数量！')
          return
        }
        for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
          this.spxxList[this.mxIndex].mxList[i].state = false
          this.spxxList[this.mxIndex].mxList.splice(i, 1, this.spxxList[this.mxIndex].mxList[i])
          this.spxxList.splice(this.mxIndex, 1, this.spxxList[this.mxIndex])
          this.checkedMxList = []
        }
      }
      if (num1 !== 0 && num2 !== 0) {
        let temp = false
        for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
          if (!this.spxxList[this.mxIndex].mxList[i].bcsl) {
            temp = true
          }
        }
        if (temp) {
          this.checked = false
          this.$toast('请先选择本次发货数量！')
          return
        }
        for (let i = 0; i < this.spxxList[this.mxIndex].mxList.length; i++) {
          if (this.spxxList[this.mxIndex].mxList[i].state === false) {
            this.checkedMxList.push(this.spxxList[this.mxIndex].mxList[i])
          }
          this.spxxList[this.mxIndex].mxList[i].state = true
          this.spxxList[this.mxIndex].mxList.splice(i, 1, this.spxxList[this.mxIndex].mxList[i])
          this.spxxList.splice(this.mxIndex, 1, this.spxxList[this.mxIndex])
        }
      }
    }
  }

}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
